@use "../../global" as g;

.c-btn {
  display: inline-block;
  width: 300px;
  font-size: 1.8rem;
  font-weight: bold;
  text-align: center;
  background: linear-gradient(135deg, #c41a7f 0%, #664bbf 100%);
  letter-spacing: 0.08em;
  line-height: 1.25;
  padding: 26px 0;
  border-radius: 50px;
  box-shadow: 0 10px 0 g.$color-secondary;
  transition: all 0.3s;

  @include g.mq(md) {
    width: 180px;
    font-size: 1.6em;
    padding: 18px;
  }
}
.c-btn:hover {
  transform: translateY(4px);
  box-shadow: 0 6px 0 g.$color-secondary;
}

.c-btn--small {
  width: 230px;
  padding: 20px 0;
}