@use "../../global" as g;

.c-nav__list {
  display: flex;
  margin-top: 36px;

  @include g.mq(md) {
    margin-top: 20px;
  }
}

.c-nav__item {
  margin-right: 30px;

  @include g.mq(md) {
    margin-right: 20px;
  }
}

.c-nav__link {
  font-size: 1.6rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  line-height: 1.125;
  transition: all 0.3s;

  @include g.mq(md) {
    font-size: 1.2em;
  }
}
.c-nav__link:hover {
  color: g.$color-primary;
}