@use "../../global" as g;

.p-session {
  width: 100%;
  background-color: #141414;
}

.p-session__body {
  padding-top: 5px;
}

.p-session__table {
  width: 100%;
  border-collapse:collapse;
}
.p-session__table th, td {
  padding: 42px 0;
  letter-spacing: 0.05em;
  border-bottom: 1px solid g.$color-primary;

  @include g.mq(md) {
    padding: 20px;
  }
}
.p-session__table th {
  font-size: 2.2rem;
  font-weight: bold;
  padding: 42px 35px;

  @include g.mq(md) {
    width: 80px;
    font-size: 1.6rem;
    padding-left: 0;
  }
}
.p-session__table td {
    padding-left: 70px;
  @include g.mq(md) {
    padding-left: 0;
  }
}
.p-session__table h3 {
  font-size: 2.2rem;
  font-weight: bold;
  color: g.$color-primary;
  margin-bottom: 16px;

  @include g.mq(md) {
    font-size: 1.6rem;
  }
}
.p-session__table p {
  font-size: 1.6rem;
  line-height: 1.5;

  @include g.mq(md) {
    font-size: 1.2rem;
  }
}