@use "../../global" as g;

.c-form {
  padding: 85px 85px 90px;
  border: 5px solid g.$color-primary;
  border-radius: 20px;

  @include g.mq(lg) {
    padding: 60px 20px;
  }
}

.c-form__item {
  margin-bottom: 36px;
}

.c-form__ttl {
  display: block;
  font-size: 1.6em;
  font-weight: bold;
  margin-bottom: 10px;

  @include g.mq(md) {
    font-size: 1.4rem;
  }
}
.c-form__ttl--mb5 {
  margin-bottom: 5px;
}

.c-form__require {
  font-size: 12px;
  font-weight: bold;
  color: g.$color-primary;
  margin-left: 10px;
}

.c-form__radio {
  font-size: 1.6em;
  font-weight: bold;

  @include g.mq(md) {
    font-size: 1.4rem;
  }
}

.c-form__input,
.c-form__textarea {
  font-size: 16px;
  color: #333;
  background-color: #fff;
  padding-left: 16px;
  border-radius: 6px;

  @include g.mq(md) {
    font-size: 1.4rem;
    padding-left: 14px;
  }
}

.c-form__input {
  width: 300px;
  height: 40px;

  @include g.mq(md) {
    width: 100%;
  }
}

.c-form__note {
  font-size: 1.2rem;
  margin-left: 12px;
}

.c-form__textarea {
  width: 600px;
  height: 255px;
  padding-top: 16px;
  padding-right: 16px;

  @include g.mq(lg) {
    width: 100%;
  }
  @include g.mq(md) {
    font-size: 1.4rem;
  }
}

.c-form__select {
  width: 300px;
  height: 40px;
  font-size: 16px;
  color: #333;
  background-color: #fff;
  padding-left: 16px;
  border-radius: 6px;

  @include g.mq(md) {
    width: 100%;
    font-size: 1.4rem;
    padding-left: 14px;
  }
}