@use "../../global" as g;

.p-mv {
  width: 100%;
  background: g.$color-gradient;
}

.p-mv__inner {
  display: flex;
  position: relative;
  height: 700px;
  background-image: url(../../../img/mv.png);
  background-repeat: no-repeat;
  background-position: right -34px bottom;
  background-size: 560px 736px;

  @include g.mq(md) {
    height: 450px;
    background-position: right -90px bottom;
    background-size: contain;
    margin: 0;
    padding: 0 20px;
  }
}

.p-mv__ttlArea{
  margin-top: 145px;

  @include g.mq(md) {
    margin-top: 90px;
    margin-left: 15px;
  }
}

.p-mv__subTtl {
  font-size: 2.4rem;
  font-weight: bold;
  color: g.$color-primary;
  letter-spacing: 0.05em;
  line-height: 1.167;
  
  @include g.mq(md) {
    font-size: 1.6rem;
  }
}

.p-mv__ttl {
  font-size: clamp(3.0rem, 10vw, 10rem);//最小値,中間値,最大値
  font-weight: bold;
  letter-spacing: 0.016em;
  line-height: 1.06;
  text-transform: uppercase;
  padding-top: 14px;
  padding-bottom: 5px;
  position: relative;
  
  @include g.mq(md) {
    padding-top: 10px;
  }
}
.p-mv__ttl::after {
  display: block;
  content: "";
  width: 326px;
  height: 68px;
  position: absolute;
    right: 66px;
    bottom: -74px;
  transform: rotate( -13deg);
  background-image: url(../../../img/teiji-de-kaerumon.svg);
  background-size: contain;

  @include g.mq(md) {
    width: 163px;
    height: 34px;
    right: 20px;
    bottom: -30px;
  }
}

.p-mv-date {
  font-size: 3.6rem;
  font-weight: bold;
  letter-spacing: 0.07em;

  @include g.mq(md) {
    font-size: 2rem;
  }
}

.p-mv__btn {
  margin-top: 26px;
}

.p-mv__speaker {
  position: absolute;
    right: -20px;
    bottom: 20px;
  
  @include g.mq(md) {
    right: 20px;
  }
}

.p-mv__speakerName {
  font-size: 2.1rem;
  font-weight: bold;
  letter-spacing: 0.1em;

  @include g.mq(md) {
    font-size: 1.8rem;
  }
}

.p-mv__speakerName--small {
  font-size: 1.6rem;
  font-weight: bold;

  @include g.mq(md) {
    font-size: 1.4rem;
  }
}

.p-mv__speakerName--label {
  display: block;
  font-size: 1.4rem;
  font-weight: bold;
  letter-spacing: -0.01em;
  margin-top: 5px;
}