@use "../../global" as g;

.c-pageTop {
  width: 80px;
  height: 80px;
  text-align: center;
  background: linear-gradient(135deg, #c41a7f 0%, #664bbf 100%);
  border-radius: 50px;
  cursor: pointer;
  position: fixed;
    right: 40px;
    bottom: 45px;
  z-index: 100;

  @include g.mq(lg) {
    width: 60px;
    height: 60px;
    right: 20px;
    bottom: 20px;
  }
}



.c-pageTop__icon {
  font-size: 40px;
  padding: 20px;

  @include g.mq(lg) {
    padding: 10px;
  }
}