@use "../global" as g;

.l-header {
  width: 100%;
  height: 90px;
  background-color: transparent;
  position: fixed;
    top: 0;
  z-index: 100;

  @include g.mq(lg) {
    display: block;
  }

  @include g.mq(md) {
    height: 74px;
  }
}

.l-header__inner {
  display: flex;
  justify-content: space-between;
}

.l-header__logo {
  margin: 28px 30px;

  @include g.mq(md) {
    margin: 10px 20px;
  }
}
