@use "global" as g;

*,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	-o-box-sizing: border-box;
	-ms-box-sizing: border-box;
	box-sizing: border-box;
}
html {
	font-size: 62.5%; // 1.0rem == 10px
	scroll-behavior: smooth;

	//breakpoint 順序確定用
	@include g.mq(xl) {
  	font-size: 62.5%; 
	}
	@include g.mq(lg) {
		font-size: 62.5%;
	}
	@include g.mq(md) {
		font-size: 62.5%;
	}
}
body {
	width: 100%;
	font-family: "Montserrat", "Noto Sans JP", Arial, "Hiragino Kaku Gothic ProN", 
							 "Hiragino Sans", Meiryo, sans-serif;
	font-size: 100%;
	color: g.$color-text;
	letter-spacing: 0.05em;
	overflow-x: hidden;
	font-feature-settings: "palt";
}
img,
video,
object {
	max-width: 100%; //はみ出しを防止
	height: auto;    //横幅の変動に対応
	border: none;
	display: block;
}