@use "../../global" as g;

.c-card {
  display: flex;
  column-gap: 60px;

  @include g.mq(md) {
    display: block;
  }
}
.c-card:nth-child(odd) {
  flex-direction: row-reverse;
}

.c-card__content {
  font-weight: bold;
  letter-spacing: 0.05em;
}

.c-card__hedding {
  margin-bottom: 41px;
}

.c-card__ttl {
  font-size: 4.1rem;
  color: g.$color-primary;
  letter-spacing: 0.065em;
  padding-top: 2px;

  @include g.mq(md) {
    font-size: 2.6rem;
  }
}

.c-card__ttlJp {
  display: block;
  font-size: 1.8rem;
  color: g.$color-text;
  letter-spacing: 0.2em;
  margin-top: 7px;

  @include g.mq(md) {
    font-size: 1.6rem;
  }
}

.c-card__txt {
  font-size: 1.6rem;
  line-height: 1.8;

  @include g.mq(md) {
    font-size: 1.2rem;
  }
}

.c-card__imgWrapper {
  flex-shrink: 0;
  width: 40%;

  @include g.mq(md) {
    width: 100px;
    float: right;
  }
}

.c-card__img {
  height: 460px;
  object-fit: contain;

  @include g.mq(md) {
    height: 100px;
  }
}