@use "../../global" as g;

.p-about{
  background-color: #141414;
}

.p-about__inner {
  display: flex;

  @include g.mq(lg) {
    display: block;
  }
}

.p-about__imgWrapper {
  flex-shrink: 0;
  width: 40%;

  @include g.mq(lg) {
    width: 100%;
    height: 150px;
  }

  @include g.mq(md) {
    height: 100px;
  }
}

.p-about__img {
  width: 100%;
  height: 650px;
  object-fit: cover;
  
  @include g.mq(lg) {
    width: 100%;
    height: 100%;
    object-position: 50% 37%;
  }
}

.p-about__content {
  width: 100%;
  font-weight: bold;
  padding-top: 90px;
  padding-left: 70px;

  @include g.mq(md) {
    padding-top: 20px;
    padding-left: 0;
  }
}

.p-about__ttlLabel {
  font-size: 2.4rem;
  letter-spacing: 0;
  margin-bottom: 20px;

  @include g.mq(md) {
    font-size: 1.8rem;
    margin-bottom: 5px;
  }
}

.p-about__ttl {
  font-size: 4.8rem;
  letter-spacing: 0.13em;
  line-height: 1.333;
  color: g.$color-primary;
  margin-bottom: 41px;

  @include g.mq(md) {
    font-size: 3.0rem;
    margin-bottom: 20px;
  }
}

.p-about__txt {
  font-size: 1.8rem;
  line-height: 1.8;
  margin-bottom: 15px;

  @include g.mq(md) {
    font-size: 1.6rem;
  }
}

.p-about__table {
  font-size: 1.6rem;
  line-height: 1.6;
  margin-bottom: 18px;
}
.p-about__table th {
  padding: 0 14px 0 0;
}
.p-about__table td {
  padding: 0;
  border-bottom: none;
}

.p-about__btn {
  
  @include g.mq(md) {
    text-align: center;
  }
}