@use "../global" as g;

.c-section {
  padding-top: 110px;
  padding-bottom: 110px;

  @include g.mq(md) {
    padding-top: 60px;
    padding-bottom: 60px;  
  }
}

.c-section__heading {
  margin-bottom: 60px;

  @include g.mq(md) {
    margin-bottom: 40px;
  }
}

.c-sectionTtl {
  font-size: 6rem;
  font-weight: bold;
  color: g.$color-primary;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;

  @include g.mq(lg) {
    font-size: 4.0rem;
  }
}
.c-sectionTtl--big {
  font-size: 6.7rem;
}

.c-sectionTtl__sub {
  display: block;
  font-size: 2.2rem;
  font-weight: bold;
  color: g.$color-text;
  letter-spacing: 0.05em;
  margin-top: 10px;
  transform: translateX( -15px);

  @include g.mq(lg) {
    font-size: 1.8rem;
    transform: translateX( 10px);
  }
}